:root {
  --background: #0a0a0a;
  --primary: #bb0a21;      /* Royal Blood Red */
  --accent: #ff1a4b;       /* Vivid Red Accent */
  --text-primary: #fafafa;
  --text-secondary: #bb0a21;
  --card-bg: #1a1a1a;
  --card-border: #bb0a21;
  --shadow-red: rgba(187, 10, 33, 0.15);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: var(--background);
  color: var(--text-primary);
  font-family: 'Poppins', Arial, sans-serif;
  margin: 0;
  min-height: 100vh;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--primary);
  font-weight: 700;
}

section, .glass-section {
  background: var(--card-bg);
  border-radius: 1.5rem;
  margin: 2rem auto;
  padding: 2rem;
  max-width: 900px;
  box-shadow: 0 4px 32px 0 var(--shadow-red);
  border: 1.5px solid var(--card-border);
  position: relative;
  z-index: 2;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--background);
}

::-webkit-scrollbar-thumb {
  background: var(--primary);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent);
}
